

import { tsvParse, csvParse } from  "d3-dsv";
import { timeParse } from "d3-time-format";

function parseData(parse) {
	return function(d) {
		d.date = parse(d.date);
		d.open = +d.open;
		d.high = +d.high;
		d.low = +d.low;
		d.close = +d.close;
		d.volume = +d.volume;

		return d;
	};
}

const parseDate = timeParse("%Y-%m-%d");

export function getData() {
	const promiseMSFT = fetch("http://btc.kkpoker.com:8090/frontend/htdocs/includes/ajax.graph.php?timeframe=1d&currency=usd")
		.then(response => response.text())
		.then(data => {
			let json = JSON.parse(data);
			let prices = json.map(d => {
				return { date:new Date(+d[0]),
									open:d[1] * (1 + (Math.random() - 0.5) * 0.1),
									high:d[1] * (1 + Math.random() * 0.1),
									low:d[1] * (1 + (Math.random() - 1) * 0.1),
									close:d[1],
									volume:d[1] * Math.random() * 0.5 }
			})
			return prices;
		})
	return promiseMSFT;
}



