import React from "react";
import PropTypes from "prop-types";

import { scaleTime } from "d3-scale";
import { utcDay } from "d3-time";
import { format } from "d3-format";
import { timeFormat } from "d3-time-format";

import { ChartCanvas, Chart } from "react-stockcharts";
import {
	BarSeries,
	AreaSeries,
	KagiSeries,
	CandlestickSeries,
	LineSeries,
	MACDSeries,
	BollingerSeries
} from "react-stockcharts/lib/series";
import { XAxis, YAxis } from "react-stockcharts/lib/axes";
import {
	CrossHairCursor,
	EdgeIndicator,
	CurrentCoordinate,
	MouseCoordinateX,
	MouseCoordinateY,
} from "react-stockcharts/lib/coordinates";

import { discontinuousTimeScaleProviderBuilder } from "react-stockcharts/lib/scale";
import { OHLCTooltip, MovingAverageTooltip, MACDTooltip, BollingerBandTooltip } from "react-stockcharts/lib/tooltip";
import { ema, sma, macd, bollingerBand, kagi } from "react-stockcharts/lib/indicator";
import { fitWidth } from "react-stockcharts/lib/helper";
import { last } from "react-stockcharts/lib/utils";

function getMaxUndefined(calculators) {
	return calculators.map(each => each.undefinedLength()).reduce((a, b) => Math.max(a, b));
}

class CandleStickChart extends React.Component {
	constructor(props) {
		super(props);
		const { data: inputData } = props;

		this.state = {
			calculators: this.getCalculators(props)
		}
	}

	getCalculators(props = this.props) {
		const { indicatorSettings } = props;
		const calculators = {};

		calculators.sma = indicatorSettings["sma"][1].map((setting, index) => {
			let name = 'sma' + index;
			return sma()
				.options({ windowSize: setting })
				.merge((d, c) => {d[name] = c;})
				.accessor(d => d[name]);
		});

		calculators.ema = indicatorSettings["ema"][1].map((setting, index) => {
			let name = 'ema' + index;
			return ema()
				.options({ windowSize: setting })
				.merge((d, c) => {d[name] = c;})
				.accessor(d => d[name]);
		});

		calculators.smaVolume = indicatorSettings["smaVolume"][1].map((setting, index) => {
			let name = 'smaVolume' + index;
			return sma()
				.options({ windowSize: setting, sourcePath: "volume" })
				.merge((d, c) => {d[name] = c;})
				.accessor(d => d[name])
				.stroke("#4682B4")
				.fill("#4682B4");
		});

		calculators.bb = bollingerBand()
			.merge((d, c) => {d.bb = c;})
			.accessor(d => d.bb);

		calculators.macd = macd()
			.options({
				fast: indicatorSettings['macd'][1]["fast"],
				slow: indicatorSettings['macd'][1]["slow"],
				signal: indicatorSettings['macd'][1]["signal"],
			})
			.merge((d, c) => {d.macd = c;})
			.accessor(d => d.macd);

		return calculators;
  }

	getChart(props = this.props) {
		const { typeSettings } = props;
		let type = null;
		let setting = null;
		let chart = null;

		for (let key in typeSettings) {
			if (typeSettings[key][0]) {
				type = key;
				setting = typeSettings[type];
				break;
			}
		}

		switch (type) {
			case "Candlestick":
				chart = (<CandlestickSeries stroke={d => setting[1].stroke[d.close > d.open ? 0: 1] }
								wickStroke={d => setting[1].stroke[d.close > d.open ? 0: 1]}
								fill={d => setting[1].stroke[d.close > d.open ? 0: 1]}
								/>);
				break;
			case "Heikin Ashi":
				chart = (<CandlestickSeries />);
				break;
			case "Kagi":
				chart = (<KagiSeries />);
				break;
			case "Line":
				chart = (<LineSeries yAccessor={d => d.close} />);
				break;
			case "Area":
				chart = (<AreaSeries yAccessor={(d) => d.close} />);
				break;
			default:
				chart = (<CandlestickSeries />);
				break;
		}

		return chart;
	}

	getIndicator(props = this.props) {
		const { indicatorSettings } = props;
		const { calculators } = this.state;
		let indicators = [];

		if (indicatorSettings["sma"][0]) {
			indicators = indicators.concat(calculators.sma.map((sma, index) => {
				return (<LineSeries key={"sma"+index} yAccessor={sma.accessor()} stroke={sma.stroke()}/>);
			}));
			indicators = indicators.concat(calculators.sma.map((sma, index) => {
				return (<CurrentCoordinate key={"csma"+index} yAccessor={sma.accessor()} fill={sma.stroke()} />);
			}));
		}

		if (indicatorSettings["ema"][0]) {
			indicators = indicators.concat(calculators.ema.map((ema, index) => {
				return (<LineSeries key={"ema"+index} yAccessor={ema.accessor()} stroke={ema.stroke()}/>);
			}));
			indicators = indicators.concat(calculators.ema.map((ema, index) => {
				return (<CurrentCoordinate key={"cema"+index} yAccessor={ema.accessor()} fill={ema.stroke()} />);
			}));
		}

		if (indicatorSettings["bb"][0]) {
			const bb = indicatorSettings["bb"];
			indicators.push(
				(<BollingerSeries key="bb" yAccessor={d => d.bb} stroke={bb[1]} fill={bb[2]} />)
			);
		}

		return indicators;
	}

	getIndicatorTooltip(props = this.props) {
		const { indicatorSettings } = props;
		const { calculators } = this.state;
		let options = [];

		if (indicatorSettings["sma"][0]) {
			options = options.concat(calculators.sma.map(sma => {
				return { yAccessor: sma.accessor(), type: sma.type(), stroke: sma.stroke(), ...sma.options(), };
			}));
		}

		if (indicatorSettings["ema"][0]) {
			options = options.concat(calculators.ema.map(ema => {
				return { yAccessor: ema.accessor(), type: ema.type(), stroke: ema.stroke(), ...ema.options(), };
			}));
		}

		if (options.length) {
			return (
				<MovingAverageTooltip
					onClick={(e) => console.log(e)}
					origin={[-38, 15]}
					options={options}
				/>
			)
		} else {
			return null;
		}
	}

	render() {
		const { data: inputData, indicatorSettings, typeSettings, width, ratio } = this.props;
		const { calculators } = this.state;

		let calculatedData = inputData;
		for (let i in indicatorSettings) {
			if (indicatorSettings[i][0]) {
				if (calculators[i] instanceof Array) {
					calculatedData = calculators[i].reduce((a, b) => { return b(a); }, calculatedData);
				} else {
					calculatedData = calculators[i](calculatedData)
				}
			}
		};

		const indexCalculator = discontinuousTimeScaleProviderBuilder().indexCalculator();
		const { index } = indexCalculator(calculatedData);
		const xScaleProvider = discontinuousTimeScaleProviderBuilder().withIndex(index);
		const { data, xScale, xAccessor, displayXAccessor } = xScaleProvider(calculatedData);

		return (
			<ChartCanvas ratio={ratio} width={width} height={600}
				margin={{ left: 70, right: 70, top: 20, bottom: 30 }}
				seriesName="MSFT"
				type="hybrid" data={data}
				xScale={xScale} xAccessor={xAccessor} displayXAccessor={displayXAccessor}>

				<Chart id={1} height={400}
					yExtents={d => [d.high, d.low]}
					padding={{ top: 10, bottom: 20 }}>
					<XAxis axisAt="bottom" orient="bottom" showTicks={false} outerTickSize={0} />
					<YAxis axisAt="right" orient="right" ticks={5} />
					<MouseCoordinateY at="right" orient="right" displayFormat={format(".2f")} />
					<EdgeIndicator itemType="last" orient="right" edgeAt="right" yAccessor={d => d.close} fill={d => d.close > d.open ? "#6BA583" : "#FF0000"}/>

					{this.getChart(this.props)}
					{this.getIndicator(this.props)}
					{this.getIndicatorTooltip(this.props)}

					<OHLCTooltip origin={[-40, 0]}/>
				</Chart>
				<Chart id={2} height={150}
					yExtents={d => d.volume}
					origin={(w, h) => [0, h - 300]}>
					{/*<YAxis axisAt="left" orient="left" ticks={5} tickFormat={format(".0s")}/>*/}

					<MouseCoordinateY at="left" orient="left" displayFormat={format(".4s")} />

					<BarSeries yAccessor={d => d.volume} fill={d => d.close > d.open ? "#6BA583" : "#DB0000" } />
					<LineSeries yAccessor={calculators.smaVolume[0].accessor()} stroke={calculators.smaVolume[0].stroke()} />
				</Chart>
				<Chart id={3} height={150}
						yExtents={calculators.macd.accessor()}
						origin={(w, h) => [0, h - 150]} padding={{ top: 10, bottom: 10 }} >
					<XAxis axisAt="bottom" orient="bottom"/>
					<YAxis axisAt="right" orient="right" ticks={2} />

					<MouseCoordinateX at="bottom" orient="bottom" displayFormat={timeFormat("%Y-%m-%d")} />
					<MouseCoordinateY at="right" orient="right" displayFormat={format(".2f")} />

					<MACDSeries yAccessor={d => d.macd} {...{stroke:indicatorSettings["macd"][2], fill:indicatorSettings["macd"][3]}} />
					<MACDTooltip
						origin={[-38, 15]}
						yAccessor={d => d.macd}
						options={calculators.macd.options()}
						appearance={{stroke:indicatorSettings["macd"][2], fill:indicatorSettings["macd"][3]}}
						/>
				</Chart>
				<CrossHairCursor />
			</ChartCanvas>
		);
	}
}

CandleStickChart.propTypes = {
	data: PropTypes.array.isRequired,
	width: PropTypes.number.isRequired,
	ratio: PropTypes.number.isRequired,
	type: PropTypes.oneOf(["Candlestick", "Heikin Ashi", "Kagi", "Line", "Area"]).isRequired,
};

CandleStickChart.defaultProps = {
	type: "Candlestick",
};
CandleStickChart = fitWidth(CandleStickChart);

export default CandleStickChart;



