
import React from 'react';
import { render } from 'react-dom';
import Chart from './Chart';
import { getData } from "./utils"

function TypeChooser(props) {
	const { onChange } = props;

	const options = ["Candlestick", "Heikin Ashi", "Kagi", "Line", "Area"].map((type, index) => {
		return (<option key={index} value={type}>{type}</option>);
	});
	return (<select onChange={onChange}>{options}</select>);
}

function IndicatorChooser(props) {
	const { onChange } = props;

	const options = ["SMA", "EMA", "MACD", "BOLL"].map((type, index) => {
		return (<option key={index} value={type}>{type}</option>);
	});
	return (<select onChange={onChange}>{options}</select>);
}

class PriceChart extends React.Component {
	constructor(props) {
		super()
	}

	componentDidMount() {
		let typeSettings = {
			"Candlestick": [true, {stroke:["#6BA583", "#DB0000"], wick:["#6BA583", "#DB0000"], fill:["#6BA583", "#DB0000"]}],
			"Heikin Ashi": [false],
			"Kagi": [false],
			"Line": [false],
			"Area": [false],
		}

		let indicatorSettings = {
			"sma": [true, [10, 20, 30]],
			"ema": [false, [10, 20, 30]],
			"smaVolume": [true, [10]],
			"macd": [true, {fast:12, slow:26, signal:9}, { macd: "#FF0000", signal: "#00F300",}, {divergence: "#CC0000"}],
			"bb": [false, {top: "#964B00",middle: "#000000",bottom: "#964B00",}, "#4682B4"]
		};

		getData().then(data => {
			this.setState({ data, typeSettings, indicatorSettings })
		})
	}

	handleTypeChange(e) {
		let typeSettings = JSON.parse(JSON.stringify(this.state.typeSettings));

		Object.keys(typeSettings).forEach(k => { typeSettings[k][0] = false; })
		typeSettings[e.target.value][0] = true;

		this.setState({ typeSettings });
	}

	handleIndicatorChange(e) {
		let indicatorSettings = JSON.parse(JSON.stringify(this.state.indicatorSettings));

		switch (e.target.value) {
			case "SMA":
				indicatorSettings["sma"][0] = true;
				indicatorSettings["ema"][0] = false;
				break;
			case "EMA":
				indicatorSettings["sma"][0] = false;
				indicatorSettings["ema"][0] = true;
				break;
			case "MACD":
				indicatorSettings["macd"][0] = true;
				break;
			case "BOLL":
				indicatorSettings["bb"][0] = true;
				break;
			default:
		}

		this.setState({ indicatorSettings });
	}

	render() {
		if (this.state == null) {
			return <div>Loading...</div>
		}
		return (
			<div>
				{/*<TypeChooser onChange={this.handleTypeChange.bind(this)} />*/}
				{/*<IndicatorChooser onChange={this.handleIndicatorChange.bind(this)} />*/}
				<Chart typeSettings={this.state.typeSettings} indicatorSettings={this.state.indicatorSettings} data={this.state.data} />
			</div>
		)
	}
}

render(
	<PriceChart />,
	document.getElementById("price_history")
);



